<template>
  <div id="archives">
      <p class="handleTitle"></p>
      <hr>
      <div id="archivesBox">
          <span :key="index" v-for="(item,index) in archiveArr">{{item}}</span>
      </div>
  </div>
</template>
<script>
export default {
  name:'archives',
  data(){
      return {
          archiveArr:['201904','201903','201902','201901','201812','201811','201810','201809','201808','201807','201806','201805','201804']
      }
  }
}
</script>
<style scoped>
#archives{
    background: #fff;
    padding: 15px 10px;
}
#archives .handleTitle{
    font-size: 18px;
    height: 38px;
}
#archives hr{
    border:1px solid #080808;
}
#archivesBox{
    margin-top: 20px;
}
#archives span{
    padding: 4px 15px;
    margin: 5px 6px;
    border-radius: 3px;
    display: inline-block;
    background: #ff5e5c;
    color: #ffffff;
    cursor:crosshair;
}
#archives span:hover{
    background: #4a4a4a;
}
</style>



